
'use client';

import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Button } from './ui/button';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from './ui/form';
import { Input } from './ui/input';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from './ui/select';
import { useToast } from '@/hooks/use-toast';
import { AlarmClockPlus } from 'lucide-react';

const reminderSchema = z.object({
  message: z.string().min(1, 'Reminder message cannot be empty.').max(100, 'Message is too long.'),
  delayMinutes: z.string(),
});

const timeOptions = Array.from({ length: 60 }, (_, i) => ({
  value: (i + 1).toString(),
  label: `${i + 1} minute${i > 0 ? 's' : ''}`,
}));

export function Reminder() {
  const { toast } = useToast();

  const form = useForm<z.infer<typeof reminderSchema>>({
    resolver: zodResolver(reminderSchema),
    defaultValues: {
      message: 'Time for a break!',
      delayMinutes: '5',
    },
  });

  const handleSetReminder = async (values: z.infer<typeof reminderSchema>) => {
    if (!('serviceWorker' in navigator) || !('Notification' in window)) {
        toast({
            variant: 'destructive',
            title: 'Browser Not Supported',
            description: 'This browser does not support notifications.',
        });
        return;
    }

    const permission = await Notification.requestPermission();
    if (permission !== 'granted') {
        toast({
            variant: 'destructive',
            title: 'Permission Denied',
            description: 'You blocked notifications. Please enable them in your browser settings.',
        });
        return;
    }

    const sw = await navigator.serviceWorker.ready;
    const delayMs = parseInt(values.delayMinutes, 10) * 60 * 1000;
    const reminderTime = new Date(Date.now() + delayMs);
    const localTime = reminderTime.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });

    sw.controller?.postMessage({
        command: 'set-reminder',
        message: values.message,
        delay: delayMs,
        localTime: localTime,
    });

    toast({
        title: 'Reminder Set!',
        description: `We'll distract you with "${values.message}" at ${localTime}.`,
    });
    form.reset();
  };

  return (
    <Form {...form}>
        <form onSubmit={form.handleSubmit(handleSetReminder)} className="space-y-4">
            <FormField
                control={form.control}
                name="message"
                render={({ field }) => (
                    <FormItem>
                        <FormLabel>Reminder Message</FormLabel>
                        <FormControl>
                            <Input placeholder="e.g., Stop working now!" {...field} />
                        </FormControl>
                        <FormMessage />
                    </FormItem>
                )}
            />
            <FormField
                control={form.control}
                name="delayMinutes"
                render={({ field }) => (
                    <FormItem>
                         <FormLabel>Distract me in...</FormLabel>
                        <Select onValueChange={field.onChange} defaultValue={field.value}>
                            <FormControl>
                                <SelectTrigger>
                                    <SelectValue placeholder="Select a time" />
                                </SelectTrigger>
                            </FormControl>
                            <SelectContent>
                                {timeOptions.map(option => (
                                    <SelectItem key={option.value} value={option.value}>
                                        {option.label}
                                    </SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                        <FormMessage />
                    </FormItem>
                )}
            />
            <Button type="submit" className="w-full">
                <AlarmClockPlus className="mr-2" />
                Set Reminder
            </Button>
        </form>
    </Form>
  );
}
