
'use client';

import { useState, useEffect, useCallback, useRef } from 'react';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Progress } from './ui/progress';

const timeOptions = [
  { value: 3, label: 'Every 3 seconds' },
  { value: 5, label: 'Every 5 seconds' },
  { value: 10, label: 'Every 10 seconds' },
  { value: 20, label: 'Every 20 seconds' },
  { value: 30, label: 'Every 30 seconds' },
  { value: 60, label: 'Every 1 minute' },
  { value: 300, label: 'Every 5 minutes' },
  { value: 600, label: 'Every 10 minutes' },
  { value: 900, label: 'Every 15 minutes' },
  { value: 1800, label: 'Every 30 minutes' },
  { value: 3600, label: 'Every 1 hour' },
  { value: 7200, label: 'Every 2 hours' },
  { value: 86400, label: 'Every 1 day' },
];

export function ScheduledDistractions() {
  const [isEnabled, setIsEnabled] = useState(false);
  const [intervalSeconds, setIntervalSeconds] = useState(300); // Default to 5 minutes
  const [progress, setProgress] = useState(0);
  const { toast } = useToast();
  const progressIntervalRef = useRef<NodeJS.Timeout | null>(null);

  // Register service worker on component mount
  useEffect(() => {
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/sw.js').catch(error => {
        console.error('Service Worker registration failed:', error);
      });
    }
  }, []);

  const stopProgress = useCallback(() => {
    if (progressIntervalRef.current) {
      clearInterval(progressIntervalRef.current);
      progressIntervalRef.current = null;
    }
    setProgress(0);
  }, []);

  const startProgress = useCallback(() => {
    stopProgress(); // Clear any existing timer
    const updateFrequency = 100; // ms
    const totalSteps = (intervalSeconds * 1000) / updateFrequency;
    let currentStep = 0;

    progressIntervalRef.current = setInterval(() => {
      currentStep++;
      setProgress((currentStep / totalSteps) * 100);
      if (currentStep >= totalSteps) {
        currentStep = 0; // Reset for next cycle
      }
    }, updateFrequency);
  }, [intervalSeconds, stopProgress]);


  const handleToggle = async (checked: boolean) => {
    if (typeof window === 'undefined' || !('Notification' in window) || !('serviceWorker' in navigator)) {
        toast({
            variant: 'destructive',
            title: 'Browser Not Supported',
            description: 'This browser does not support notifications.',
        });
        setIsEnabled(false);
        return;
    }
    
    const serviceWorker = await navigator.serviceWorker.ready;

    if (checked) {
      if (Notification.permission !== 'granted') {
        const permission = await Notification.requestPermission();
        if (permission === 'granted') {
            toast({
                title: 'Distractions Enabled!',
                description: "Get ready to be wonderfully unproductive.",
            });
            setIsEnabled(true);
            serviceWorker.controller?.postMessage({ command: 'start', interval: intervalSeconds * 1000 });
            startProgress();
        } else {
            toast({
              variant: 'destructive',
              title: 'Permission Denied',
              description: 'You blocked notifications. Please enable them in your browser settings.',
            });
            setIsEnabled(false);
        }
      } else {
         toast({
            title: 'Distractions Enabled!',
            description: "Get ready to be wonderfully unproductive.",
        });
        setIsEnabled(true);
        serviceWorker.controller?.postMessage({ command: 'start', interval: intervalSeconds * 1000 });
        startProgress();
      }
    } else {
      setIsEnabled(false);
      serviceWorker.controller?.postMessage({ command: 'stop' });
      stopProgress();
      toast({
            title: 'Distractions Disabled',
            description: "Okay, back to work... for now.",
      });
    }
  };
  
  const handleIntervalChange = (value: string) => {
    const newInterval = Number(value);
    setIntervalSeconds(newInterval);
    // If already enabled, restart the timer with the new interval
    if (isEnabled) {
       navigator.serviceWorker.ready.then(sw => {
           sw.controller?.postMessage({ command: 'start', interval: newInterval * 1000 });
           startProgress();
       });
    }
  }

  const getFrequencyLabel = () => {
    return timeOptions.find(o => o.value === intervalSeconds)?.label.replace('Every ', '');
  }

  return (
    <div className="space-y-4">
      <div className="flex items-center justify-between rounded-lg bg-muted/50 p-4">
         <Label htmlFor="distraction-mode" className="font-semibold flex-1 pr-4">
            Enable Distractions
          </Label>
        <Switch
          id="distraction-mode"
          checked={isEnabled}
          onCheckedChange={handleToggle}
          aria-label="Enable or disable scheduled distractions"
        />
      </div>

       {isEnabled && (
        <div className="px-1 space-y-3">
            <p className="text-sm text-muted-foreground">
                Next distraction in {getFrequencyLabel()}. Stay focused... or don't.
            </p>
            <Progress value={progress} className="w-full h-2" />
        </div>
       )}

      <div>
        <Label htmlFor="distraction-interval">Distraction Frequency</Label>
        <Select
            value={String(intervalSeconds)}
            onValueChange={handleIntervalChange}
        >
            <SelectTrigger id="distraction-interval">
                <SelectValue placeholder="Select interval" />
            </SelectTrigger>
            <SelectContent>
                {timeOptions.map(option => (
                    <SelectItem key={option.value} value={String(option.value)}>
                        {option.label}
                    </SelectItem>
                ))}
            </SelectContent>
        </Select>
         <p className="text-xs text-muted-foreground mt-2">Notifications will appear even if the app is in the background.</p>
      </div>
    </div>
  );
}
